/************************************************navbar style*******************************************************************/

.css-11b3ww9-MuiPaper-root-MuiAppBar-root {
  background-color: white !important;
  box-shadow: 0px 2px 2px lightgray !important;
  display: flex !important;
  justify-content: space-between;
  align-items: center;
}
.css-11b3ww9-MuiPaper-root-MuiAppBar-root img {
  width: 250px;
  margin-left: 20px;
}

.css-11b3ww9-MuiPaper-root-MuiAppBar-root a , header a {
  color: #757575 !important;
  padding: 10px !important;
  display: inline-block !important;
  text-decoration: none !important;
  transition: 1s !important;
}
.css-11b3ww9-MuiPaper-root-MuiAppBar-root a:hover , header a:hover {
  color: #ef3575 !important;
  cursor: pointer !important;
  transition: 1s;
}
.css-11b3ww9-MuiPaper-root-MuiAppBar-root a:active , header a:active {
  color: #ef3575 !important;
}

/* ***********************************************************footer style **************************************************************/
.footer {
  padding: 70px 40px;
}
.footer a {
  color: white;
  text-decoration: none;
  cursor: pointer;
}
.icons {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}
.icons a {
  color: #293e5a;
  display: flex;
  align-items: center;
  align-content: center;
  text-decoration: none;
  padding: 3px 6px;
  padding-top: 6px;
  display: inline-block;
  border-radius: 50%;
  background-color: white;
}
/* background */

.home-parent {
  position: relative !important;
}
.home {
  width: 95vw;
  margin: auto;
  height: 80vh;
  border-radius: 0px 0px 50px 50px;
  background-image: url("https://static.wixstatic.com/media/b54bb7_a5b7867aa6c048eb8f8d953dfce79cd2~mv2.png/v1/fill/w_1810,h_694,al_c,q_90,usm_0.66_1.00_0.01,enc_auto/model-woman-talking-phone.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  object-fit: cover;
  display: flex;
  align-items: center;
  background-position: center;
  position: relative;
}
.css-i4bv87-MuiSvgIcon-root {
  width: 15px !important;
  height: 15px !important;
}
.home .parent {
  padding: 70px 50px;
  border-radius: 50px;
}
.esim-parent .home {
  background-image: url("https://static.wixstatic.com/media/b54bb7_0bd96973a89d4a9d9e31b9b82b48e22d~mv2.png/v1/fill/w_1804,h_724,al_c,q_90,usm_0.66_1.00_0.01,enc_auto/shutterstock_1191546529_edited.png");
}
.h-parent .home {
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: 50vw;
  background-position: right;
  background-color: white;
  background-image: url("https://static.wixstatic.com/media/b54bb7_2db9d1257e214cf88006d6208ebea815~mv2.png/v1/fill/w_855,h_489,al_c,q_90,usm_0.66_1.00_0.01,enc_auto/Untitled-1.png");
}
.home .parent span {
  display: block;
  margin-bottom: 20px;
}
.home h1 {
  font-size: 36px;
  font-weight: bold;
}
.home form input {
  display: block;
  padding: 10px;
  padding-left: 35px;
  border-radius: 10px;
  border-color: greenyellow;
  border-style: solid;
  width: 60%;
  margin-top: 20px;
}
.home .left,
.meet .left {
  position: absolute;
  left: 0px;
  top: 50%;
  transform: translateY(-50%);
  height: 300px;
}
.home .right,
.meet .right {
  position: absolute;
  right: 0px;
  top: 50%;
  transform: translateY(0%) scale(-1);
  height: 300px;
}
.home-parent .third {
  background-color: white;
  border-radius: 50px;
  padding: 50px;
  margin: auto;
  margin-top: 50px;
  min-height: 90vh;
  background-image: url("https://static.wixstatic.com/media/b54bb7_69df8b1b389f484280be3facaaf47a3e~mv2.jpg/v1/fill/w_443,h_295,al_c,q_80,usm_0.66_1.00_0.01,enc_auto/dsdsa.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 400px 250px;
  background-attachment: scroll;
}

.virtual-parent .home {
  background-color: white;
  border-radius: 50px;
  padding: 50px;
  margin: auto;
  margin-top: 50px;
  min-height: 90vh;
  background-image: url("https://static.wixstatic.com/media/bdd318_5a4407a933f34e67b0b13ef6b90630f6~mv2.jpg/v1/fill/w_1796,h_740,al_c,q_85,usm_0.66_1.00_0.01,enc_auto/shutterstock_2303495945%20-%20Copy.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  object-fit: cover;
  background-attachment: scroll;
}
.h-parent .home a,
.h-parent,
.css-i4bv87-MuiSvgIcon-root {
  color: #757575;
  transition: 0.7s;
  font-size: 20px;
  text-decoration: none;
  text-align: left !important;

}
.h-parent .home a:hover {
  transition: 0.7s;
  color: #ef3575;
}
@media only screen and (max-width: 900px) {
  .links {
    transition: 1s;
    position: fixed;
    top: 0px;
    right: -100%;
    height: 100vh;
    width: 100%;
    display: flex;
    font-size: 25px;
    justify-content: center;
    align-items: center;

    background-color: white;
  }
  .links .nav {
    flex-direction: column;
  }
  .links a {
    color: black !important;
    text-align: center !important;
    display: block !important;
  }
  .links a:nth-child(4) {
    color: #757575 !important;
  }
  .links img {
    display: block;
    margin-top: 100px;
  }
  .home {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .home .parent ,   #parent  {
    transform: translateX(0px) !important;
    width: 90% !important;
    position: absolute;
  }
  .virtual-parent {
    position: relative;
  }

 
  
  .home .parent input {
    width: 80%;
  }
  .home .left,
  .meet .left {
    height: 200px;
  }
  .home .right,
  .meet .right {
    height: 200px;
  }
}

.hover:hover {
  background-color: white !important;
  color: black;
  border: 2px solid black;
  transition: 1s;
}
